.navbar {
    z-index: 2;
    padding: 2rem;
}

.navbar.colorChange {
    /* background-image: radial-gradient( circle 610px at 5.2% 51.6%,  rgba(5,8,114,1) 0%, rgba(7,3,53,1) 97.5% )!important; */
    background-color: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2);
}


.navbar__container {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

/*----------Nav Buttons-----------*/
nav a.nav-link {
    letter-spacing: 0.0375rem;
    cursor: pointer;
    font-size: 16px;

}

nav ul li {
    text-transform: uppercase;
    color: #000;

}

.navbar-nav>li>a {
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    color: #000 !important;
    border-top: 0.1875rem rgba(0, 0, 0, 0);
}

.navbar-brand {
    max-width: 35rem;
    padding: 0 !important;
    font-size: large;
    height: auto;
    text-align: center;
    align-items: center;
    margin-left: 25px;
    color: black !important;
}

.navbar-nav>li>a.active,
.navbar-nav>li>a:focus {
    color: #EA0000 !important;
}

.navbar-nav>li>a:hover {
    opacity: 0.3;
}

@media(max-width: 768px) {
    .navbar-brand {
        width: 7rem;
    }
}