.about {
    background-color: #fff;
    /* height: 90vh; */


}

.about h1 {
    color: #000;
    padding: 1.3rem 0;
    text-transform: uppercase;
    font-size: 2rem;
    text-align: center;
}




.profile__img {
    width: 100%;
    border-radius: 15px;
}


.profile__wrap {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 5rem;

}

.about__component {
    margin-top: 4rem;


}

.about__position {
    font-size: 14px;
    font-weight: bold;
}

.about__span {
    font-size: 14px;
    color: #6d6a7c;
}





.education__container {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 2rem;
}

.education__card {
    margin: 10px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    width: 150px;
    height: 200px;
    cursor: pointer;
}

.education__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.education__image:hover {
    opacity: 0;
}








.card {
    overflow: hidden;
    height: 200px;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    /* box-shadow: 20px 20px 60px #9d9d9d, -20px -20px 60px #ffffff; */
    margin-right: 1rem;
}


.card img {
    width: 200px;
    height: 200px;
    border-radius: 15px;
}

.card:hover {
    transform: translateY(-1%);

}

.card:hover .content {
    top: -65%;
    opacity: 1;
}

.content {
    padding: 2rem 0.7rem;
    position: relative;
    opacity: 0;
    font-family: "Josefin Sans", Arial;
    display: flex;
    box-shadow: none;
    backdrop-filter: blur(10px);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 0;
    text-align: center;
}

.content h1 {
    font-size: 16px;
    color: red;
    margin: 0.7rem 0;
    margin-top: 2rem;
    font-weight: bold;
}

.content small {
    font-weight: bold;
}

.content h3 {
    color: #fff;
    font-size: 15px;
    font-weight: 300;
    letter-spacing: 2px;
    line-height: 1.6;
}

.education__button {
    display: inline-block;
    background: linear-gradient(0deg, #EA0000, #FF0078);
    color: #fff;
    padding: 1rem;
    border: none;
    border-radius: 0.5rem;
    font-weight: var(--font-medium);
    text-decoration: none;
    margin-bottom: 3rem;
}

.education__button:hover {
    opacity: 85%;
    box-shadow: 0 7px 12px rgba(0, 0, 0, 0.1);
    color: #fff;

}

.card-text {
    font-size: 13px;
}

.card-subtitle {
    font-size: 14px;
}

.card__links {
    text-decoration: none;
    color: #000;
}

.card__links:hover {
    color: #000;
}





@media(max-width:480px) {
    /* .about {
        height: 130vh;
    } */
}

@media(max-width:768px) {

    .about__button {
        display: flex;
        justify-content: center;
    }

    .education__container {
        display: flex;
        justify-content: center;
        margin-bottom: 2rem;
    }

    .school-heading {
        font-size: 0.95rem;
    }

    .about__description {
        font-size: medium;
        text-align: center;
    }

    .about__position {
        text-align: center;
    }

    .profile__wrap {
        justify-content: center;
        margin-bottom: 1rem;
    }

    .profile__img {
        width: 50%;
    }

    .card img {
        width: 150px;
        height: 150px;
        object-fit: cover;
    }

    .card {
        width: 150px;
        height: 150px;
        margin: 5px;
    }

    .content {
        font-size: 0.7rem;

    }

    .content h1 {
        font-size: 12px;
        margin-top: 0.5rem;
    }

    .about__component {
        margin-top: 0;
    }

    /* .about {
        height: 130vh;
    } */

    .card-text {
        font-size: 10px;
    }

    .card-subtitle {
        font-size: 11px;
    }

    .card-title {
        font-size: 12px;
    }

    .education__hfive {
        text-align: center;
    }
}

@media(max-width:1024px) {
    /* .about {
        height: 120vh;
    } */

    .card-text {
        font-size: 10px;
    }
}