/********************************************FOOTER********************************************/
.footer {
    /* background:var(--primary-dark-gray); */
    background: #fff;
    padding: 3rem;
}

.footer a {
    text-decoration: none;
    color: var(--primary-gray);
}

.footer a:hover {
    text-decoration: none;
    color: var(--orange);
    cursor: pointer;
}

.footer p {
    margin-bottom: 0;
    color: #000;
}

.footer-lastname {
    color: #EA0000 !important;
}

@media(max-width: 768px) {
    .copyright-footer {
        font-size: small;
    }

    .col-md-4 {
        margin: 5px;
    }

}