.navbar {
    z-index: 2;
    padding: 2rem;
}

.navbar.colorChange {
    /* background-image: radial-gradient( circle 610px at 5.2% 51.6%,  rgba(5,8,114,1) 0%, rgba(7,3,53,1) 97.5% )!important; */
    background-color: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2);
}


.navbar__container {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

/*----------Nav Buttons-----------*/
nav a.nav-link {
    letter-spacing: 0.0375rem;
    cursor: pointer;
    font-size: 16px;

}

nav ul li {
    text-transform: uppercase;
    color: #000;

}

.navbar-nav>li>a {
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    color: #000 !important;
    border-top: 0.1875rem rgba(0, 0, 0, 0);
}

.navbar-brand {
    max-width: 35rem;
    padding: 0 !important;
    font-size: large;
    height: auto;
    text-align: center;
    align-items: center;
    margin-left: 25px;
    color: black !important;
}

.navbar-nav>li>a.active,
.navbar-nav>li>a:focus {
    color: #EA0000 !important;
}

.navbar-nav>li>a:hover {
    opacity: 0.3;
}

@media(max-width: 768px) {
    .navbar-brand {
        width: 7rem;
    }
}
.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    height: 100vh;
}

.home::before {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    content: "";
    position: absolute;
    background: linear-gradient(0deg, #EA0000, #FF0078);
    -webkit-clip-path: circle(30% at left 90%);
            clip-path: circle(30% at left 90%);
}

.home::after {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    content: "";
    position: absolute;
    background: linear-gradient(45deg, #EA0000, #FF0078);
    -webkit-clip-path: circle(20% at right 10%);
            clip-path: circle(20% at right 10%);
}

.home__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 1;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__hone {
    color: #000;
}


.home__image {
    width: 500px;
    z-index: -1;
    margin-bottom: -150px;
    -webkit-mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
            mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.typed__text {
    font-size: 2rem;
    color: #6d6a7c;
    margin-bottom: 0.5rem;
}

.home__button {
    display: inline-block;
    background: linear-gradient(0deg, #EA0000, #FF0078);
    color: #fff;
    padding: 1rem;
    border: none;
    border-radius: 0.5rem;
    text-decoration: none;
}

.home__button:hover {
    opacity: 85%;
    box-shadow: 0 7px 12px rgba(0, 0, 0, 0.1);
    color: #fff;
}

@media(max-width:768px) {
    .typed__text {
        font-size: 16px;
    }

    .home__button {
        font-size: 12px;
        padding: 0.5rem;

    }
}
.about {
    background-color: #fff;
    /* height: 90vh; */


}

.about h1 {
    color: #000;
    padding: 1.3rem 0;
    text-transform: uppercase;
    font-size: 2rem;
    text-align: center;
}




.profile__img {
    width: 100%;
    border-radius: 15px;
}


.profile__wrap {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 5rem;

}

.about__component {
    margin-top: 4rem;


}

.about__position {
    font-size: 14px;
    font-weight: bold;
}

.about__span {
    font-size: 14px;
    color: #6d6a7c;
}





.education__container {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 2rem;
}

.education__card {
    margin: 10px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    width: 150px;
    height: 200px;
    cursor: pointer;
}

.education__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.education__image:hover {
    opacity: 0;
}








.card {
    overflow: hidden;
    height: 200px;
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    /* box-shadow: 20px 20px 60px #9d9d9d, -20px -20px 60px #ffffff; */
    margin-right: 1rem;
}


.card img {
    width: 200px;
    height: 200px;
    border-radius: 15px;
}

.card:hover {
    -webkit-transform: translateY(-1%);
            transform: translateY(-1%);

}

.card:hover .content {
    top: -65%;
    opacity: 1;
}

.content {
    padding: 2rem 0.7rem;
    position: relative;
    opacity: 0;
    font-family: "Josefin Sans", Arial;
    display: flex;
    box-shadow: none;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 0;
    text-align: center;
}

.content h1 {
    font-size: 16px;
    color: red;
    margin: 0.7rem 0;
    margin-top: 2rem;
    font-weight: bold;
}

.content small {
    font-weight: bold;
}

.content h3 {
    color: #fff;
    font-size: 15px;
    font-weight: 300;
    letter-spacing: 2px;
    line-height: 1.6;
}

.education__button {
    display: inline-block;
    background: linear-gradient(0deg, #EA0000, #FF0078);
    color: #fff;
    padding: 1rem;
    border: none;
    border-radius: 0.5rem;
    font-weight: var(--font-medium);
    text-decoration: none;
    margin-bottom: 3rem;
}

.education__button:hover {
    opacity: 85%;
    box-shadow: 0 7px 12px rgba(0, 0, 0, 0.1);
    color: #fff;

}

.card-text {
    font-size: 13px;
}

.card-subtitle {
    font-size: 14px;
}

.card__links {
    text-decoration: none;
    color: #000;
}

.card__links:hover {
    color: #000;
}





