}

@media(max-width:768px) {

    .about__button {
        display: flex;
        justify-content: center;
    }

    .education__container {
        display: flex;
        justify-content: center;
        margin-bottom: 2rem;
    }

    .school-heading {
        font-size: 0.95rem;
    }

    .about__description {
        font-size: medium;
        text-align: center;
    }

    .about__position {
