/**********************************************Project***********************************************/
.project {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    background-color: #eee;

}

.project__container {
    max-width: 1500px;
}

.project__container h1 {
    color: #000;
    padding: 1.3rem 0;
    text-transform: uppercase;
    font-size: 2rem;
    text-align: center;
}

.project__row {
    display: flex;
    justify-content: center;
    z-index: 1;

}

.project__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    min-width: 100px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    z-index: 1;
}

.project__card>img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}


.project__card>a {
    text-align: right;
    margin-left: auto;
}

.project__circle {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
    width: 3rem;
    height: 3rem;
    background: #EA0000;
    border-radius: 1.625rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.project__icon {
    font-size: 20px;
    color: white;
}



.project__card>a:hover {
    opacity: 50%;
}

.project__description {
    font-size: 16px;
    color: #000;
    margin: 1.5rem;
}

.project__info {
    font-size: 18px;
}


@media screen and (max-width: 650px) {
    .project__info {
        font-size: 10px;
    }

    .project__circle {
        width: 1.35rem;
        height: 1.35rem;
        margin: 0;
    }

    .project__icon {
        font-size: 12px;
    }


}

@media screen and (max-width: 480px) {

    .project__description {
        font-size: 8px;
        margin: 0;
    }

    .project__info {
        font-size: 10px;
    }

    .project__circle {
        width: 1.35rem;
        height: 1.35rem;
        margin: 0;
    }

    .project__icon {
        font-size: 12px;
    }
}
/******************************** Experience *************************************/
.experience {
    padding: 0.7rem;
    /* background-image: linear-gradient(to top, #0c3483 0%, #a2b6df 100%, #6b8cce 100%, #a2b6df 100%); */
    /* background-image: linear-gradient(to top, #0c3483 0%, #fff 100%, #fff 100%, #ffffff 100%);  */
    background-color: #fff;
}

.experience-wrapper {
    position: relative;
    padding: 3.125rem 0;
    overflow: hidden;

}

.experience h1 {
    color: #000;
    padding: 1.3rem 0;
    text-transform: uppercase;
    font-size: 2rem;
    text-align: center;
}

.experience-wrapper::before {
    position: absolute;
    content: "";
    top: 0;
    left: 50%;
    margin-left: -0.0625rem;
    width: 0.125rem;
    height: 100%;
    background: var(--primary-gray);
    z-index: 1;
}

.timeline-block {
    width: calc(50% + 0.5rem);
    display: flex;
    justify-content: space-between;
    clear: both;
}

.timeline-block-right {
    float: right;
}

.timeline-block-left {
    float: left;
    direction: rtl;
}

.marker {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 0.3125rem solid var(--primary-black);
    background: #EA0000;
    margin-top: 0.625rem;
    z-index: 1;
}

.timeline-content {
    width: 95%;
    padding: 0 0.9375rem;
    color: darkgray;
    direction: ltr;
}

.timeline-content h3 {
    margin-top: 0.3125rem;
    color: #000;
    font-weight: 900;
}

/*------------- MEDIA -------------*/
@media(max-width:768px) {
    .experience .container {
        padding-left: 0;
    }

    .experience-wrapper::before {
        left: 0.5rem;
        margin-bottom: 1.875rem;
    }

    .timeline-block-right {
        float: none;
        width: 100%;
    }

    .timeline-block-left {
        float: none;
        direction: ltr;
        width: 100%;
    }
}
