/*------------------INPUTS--------------*/
.contacts input .contacts textarea {
    width: 50%;
    box-sizing: border-box;
    background: none;
    resize: none;
    border: 0;
    border-bottom: 0.125rem solid #EA0000;
    border-radius: 0;
    color: #000 !important;
    outline: 0 !important;
    box-shadow: none !important;
    top: 0;
}

.contacts .container {
    padding: 4rem 0;
}


.contacts textarea {
    height: 12.4rem !important;
}


.line {
    position: relative;
    top: -0.8rem;
    width: 0;
    height: 0.125rem;
    background: #EA0000;
    display: inline-block;
}

input:focus,
input.form-control:focus {

    outline: none !important;
    outline-width: 0 !important;
    box-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
}

textarea:focus,
textarea.form-control:focus {
    outline: none !important;
    outline-width: 0 !important;
    box-shadow: none;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
}

input.form-control {
    margin: -0.3rem 0;
}

textarea.form-control {
    margin: -0.3rem 0;
}

.error-message {
    color: #000;
    background: var(--primary-hover-red);
    text-align: left;
}

.success-message {
    font-weight: 900;
    color: green;

}

.contact__button {
    display: inline-block;
    background: linear-gradient(0deg, #EA0000, #FF0078);
    color: #fff;
    padding: 1rem;
    border: none;
    border-radius: 0.5rem;
    text-decoration: none;
    width: 120px;
}

.recaptcha {
    display: flex;
    justify-content: center;
}

.error-captcha {
    color: red;
    margin-bottom: 14px;
    text-align: center;
}

/*-----------MEDIA----------*/
@media(max-width: 768px) {
    .contacts {
        overflow-x: hidden;
    }

    .contacts .container {
        padding: 0 1rem;
    }

    .contacts p {
        padding: 0 0.4rem;
    }

}
