.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    height: 100vh;
}

.home::before {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    content: "";
    position: absolute;
    background: linear-gradient(0deg, #EA0000, #FF0078);
    clip-path: circle(30% at left 90%);
}

.home::after {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    content: "";
    position: absolute;
    background: linear-gradient(45deg, #EA0000, #FF0078);
    clip-path: circle(20% at right 10%);
}

.home__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 1;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__hone {
    color: #000;
}


.home__image {
    width: 500px;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.typed__text {
    font-size: 2rem;
    color: #6d6a7c;
    margin-bottom: 0.5rem;
}

.home__button {
    display: inline-block;
    background: linear-gradient(0deg, #EA0000, #FF0078);
    color: #fff;
    padding: 1rem;
    border: none;
    border-radius: 0.5rem;
    text-decoration: none;
}

.home__button:hover {
    opacity: 85%;
    box-shadow: 0 7px 12px rgba(0, 0, 0, 0.1);
    color: #fff;
}

@media(max-width:768px) {
    .typed__text {
        font-size: 16px;
    }

    .home__button {
        font-size: 12px;
        padding: 0.5rem;

    }
}