.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&family=Rubik:wght@500&display=swap');

:root {
  --primary-black: rgb(0, 0, 0);
  --primary-white: #fffff;
  --primary-red: #ef4035;
  --primary-gray: #ccd1d9;
  --primary-dark-gray: #666666;
  --primary-light-gray: #f4f4f4;
  --primary-hover-red: #f33501;
  --dark-blue: rgb(25, 3, 85);
  --light-gray: #f8f9fa;
  --orange: #f9ab00;
  --transparent-black: rgba(0, 0, 0, 0.9);
  --primary-blue: #06c;
}

body {
  font-family: "Roboto", sans-serif !important;
}

